@import "/src/variables.scss";

.filter {
  width: 400px;
  @include media-tablet-landscape {
    width: 221px;
  }

  @include media-tablet-portrait {
    width: 231px;
  }

  @include media-mobile-landscape {
    display: none;
  }

  @include media-mobile-portrait {
    display: none;
  }
}

.filter-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.filter-header-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 50%;
  h3 {
    position: relative;
    left: 10px;
    font-family: "Montsserrat SemiBold";
  }
}

.grant-filter-divisor {
  width: 0px;
  border-bottom: 50px solid white;
  border-left: 40px solid transparent;
  border-right: 40px solid transparent;
  transform: rotate(135deg);
  position: relative;
  top: 20px;
  left: 30px;
}

.filter-header-reset {
  height: 50px;
  width: 50%;
  border-top-right-radius: 30px;
  background-color: #ffffff;
  & button {
    height: 50px;
    width: 142px;
    border-radius: 100px;
    @include media-tablet-landscape {
      height: 30px;
      width: 87px;
    }

    @include media-tablet-portrait {
      height: 30px;
      width: 87px;
    }

    @include media-mobile-landscape {
      height: 38px;
      width: 104px;
    }

    @include media-mobile-portrait {
      height: 38px;
      width: 104px;
    }

    border: 1px solid #99a7b2;
    background-color: #ffffff;
    position: relative;
    top: 25%;
    left: -10%;
  }
}

.filter-body {
  background-color: #ffffff;
  border-top-left-radius: 30px;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  padding: 20px 25px;
  .direction {
    margin: 15px 0px;
    color: #6e7880;
    label {
      margin-left: 30px;
      font-size: 21px;
      position: relative;
      top: 8px;
      left: 8px;
      @include media-tablet-landscape {
        font-size: 12px;
        top: -6px;
        left: 0px;
      }

      @include media-tablet-portrait {
        font-size: 12px;
        top: -6px;
        left: 0px;
      }

      @include media-mobile-landscape {
        font-size: 16px;
        top: 0px;
        left: 0px;
      }

      @include media-mobile-portrait {
        font-size: 14px;
        top: 0px;
        left: 0px;
      }
    }
  }

  .direction input[type="checkbox"]:before {
    position: relative;
    right: 2px;
    bottom: 2px;
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid #dbe4eb;
    border-radius: 6px;
    content: "";
    background: #dbe4eb;
    @include media-tablet-landscape {
      height: 20px;
      width: 20px;
      bottom: 8px;
    }

    @include media-tablet-portrait {
      height: 20px;
      width: 20px;
      bottom: 8px;
    }

    @include media-mobile-landscape {
      height: 24px;
      width: 24px;
      bottom: 4px;
    }

    @include media-mobile-portrait {
      height: 24px;
      width: 24px;
      bottom: 4px;
    }
  }
  .direction input[type="checkbox"]::after {
    position: relative;
    display: block;
    width: 30px;
    right: 2px;
    bottom: 34px;
    height: 30px;
    border: 1px solid #dbe4eb;
    border-radius: 6px;
    content: "";
    @include media-tablet-landscape {
      height: 20px;
      width: 20px;
      bottom: 30px;
    }

    @include media-tablet-portrait {
      height: 20px;
      width: 20px;
      bottom: 30px;
    }

    @include media-mobile-landscape {
      height: 24px;
      width: 24px;
    }

    @include media-mobile-portrait {
      height: 24px;
      width: 24px;
    }
  }
  .direction input[type="checkbox"]:checked::after {
    background: url("../../../assets/check.svg");
    background-size: contain;
  }
  .amount {
    display: flex;
    margin: 25px 0px;
    color: #6e7880;
    label {
      margin-left: 30px;
      font-size: 21px;
      left: 8px;
      @include media-tablet-landscape {
        font-size: 12px;
        left: 0px;
      }

      @include media-tablet-portrait {
        font-size: 12px;
      }

      @include media-mobile-landscape {
        font-size: 16px;
      }

      @include media-mobile-portrait {
        font-size: 14px;
      }
    }
  }
  .amount input[type="radio"] {
    width: 30px;
    height: 30px;
    -webkit-appearance: none;
    appearance: none;
    background-color: #dbe4eb;
    border-radius: 50%;
    margin: 0;
    position: relative;
    @include media-tablet-landscape {
      height: 20px;
      width: 20px;
    }

    @include media-tablet-portrait {
      height: 20px;
      width: 20px;
    }

    @include media-mobile-landscape {
      height: 24px;
      width: 24px;
    }

    @include media-mobile-portrait {
      height: 24px;
      width: 24px;
    }
  }

  .amount label {
  }

  .amount input[type="radio"]:checked {
    background-color: #dbe4eb;
  }

  .amount input[type="radio"]:checked::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 16px;
    height: 16px;
    background-color: #2d4ef8;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    @include media-tablet-landscape {
      height: 11px;
      width: 11px;
    }

    @include media-tablet-portrait {
      height: 11px;
      width: 11px;
    }

    @include media-mobile-landscape {
      height: 13px;
      width: 13px;
    }

    @include media-mobile-portrait {
      height: 13px;
      width: 13px;
    }
  }

  h4 {
    font-family: "Montsserrat SemiBold";
    margin: 30px 0px;
    @include media-tablet-landscape {
      margin: 20px 0px;
      font-size: 14px;
    }

    @include media-tablet-portrait {
      margin: 20px 0px;
      font-size: 14px;
    }

    @include media-mobile-landscape {
      margin: 15px 0px;
      font-size: 16px;
    }

    @include media-mobile-portrait {
      margin: 15px 0px;
      font-size: 16px;
    }
  }
}

.filter-datepicker {
  display: flex;
  justify-content: center;
  border-radius: 14px;
  margin-top: 15px;
  box-shadow: 0 0 16px rgba(138, 153, 176, 0.12);
  * {
    color: #6e7880;
    background-color: #ffffff;
    border: none;
  }
}

.filter-date-group {
  display: flex;
  justify-content: space-between;
  font-family: "Open Sans Regular";
  font-size: 14px;
  label {
    @include media-tablet-landscape {
      font-size: 12px;
    }

    @include media-tablet-portrait {
      font-size: 12px;
    }

    @include media-mobile-landscape {
      font-size: 16px;
    }

    @include media-mobile-portrait {
      font-size: 16px;
    }
  }
}

.filter-date-indicator {
  &:first-child {
    border-right: 1px solid #e0e4e7;
  }
  input {
    height: 35px;
    padding-left: 40px;
    font-size: 21px;
    @include media-tablet-landscape {
      padding-left: 25px;
      font-size: 12px;
    }

    @include media-tablet-portrait {
      padding-left: 25px;
      font-size: 10px;
    }

    @include media-mobile-landscape {
      padding-left: 25px;
      font-size: 14px;
    }

    @include media-mobile-portrait {
      padding-left: 25px;
      font-size: 14px;
    }
  }
  position: relative;
  width: 160px;
  @include media-tablet-landscape {
    width: 100px;
  }

  @include media-tablet-portrait {
    width: 100px;
  }

  @include media-mobile-landscape {
  }

  @include media-mobile-portrait {
  }
  .filter-date-icon {
    top: 25px;
    left: 5px;
    height: 30px;
    width: 30px;
    @include media-tablet-landscape {
      left: 0px;
      height: 16px;
      width: 16px;
    }

    @include media-tablet-portrait {
      left: 0px;
      height: 20px;
      width: 20px;
    }

    @include media-mobile-landscape {
      left: 0px;
      height: 24px;
      width: 24px;
    }

    @include media-mobile-portrait {
      left: 0px;
      height: 24px;
      width: 24px;
    }
    background: url("../../../assets/calendar.svg");
    background-size: contain;
    position: absolute;
    cursor: pointer;
    &.active {
      background: url("../../../assets/calendar-active.svg");
      background-size: contain;
    }
  }
}

.active {
  .mantine-DateInput-input {
    color: #2d4ef8;
  }
}

.mantine-DateInput-input {
  color: #99a7b2;
  border: none;
  &:disabled {
    background-color: transparent;
  }
}
.mantine-DateInput-label {
  color: #99a7b2;
  position: relative;
  left: 35px;
  @include media-tablet-landscape {
    display: block;
    width: 100px;
    left: 0px;
  }

  @include media-tablet-portrait {
    display: block;
    width: 100px;
    left: 0px;
  }

  @include media-mobile-landscape {
    display: block;
    width: 100px;
    left: 0px;
  }

  @include media-mobile-portrait {
    display: block;
    width: 100px;
    left: 0px;
  }
}
